<template>
	<view :class="{body:models=='1'||models=='', bodyNight:models=='0'}">
		<!-- 删除模态窗 -->
		<u-modal :show="show" content="确定要删除做题记录吗?将无法恢复哟!" :showCancelButton='true' :closeOnClickOverlay="true" @close="this.show=false" @cancel="this.show=false" @confirm="resets"></u-modal>
		<view class="tabar">
			<view v-if="item.title" :class="{checked:item.isChecked}" v-for="(item,index) in data" :key="index" @click="change(index)">{{item.title}}</view>
		</view>
		<view class="info" v-if="type==0">
			<view class="info_left">
				<view>今日已刷题数</view>
				<view><view class="text">{{today_brush}}</view>题</view>
			</view>
			<view class="boders"></view>
			<view class="info_right">
				<view>总共已刷题数</view>
				<view><view class="text">{{total_brush}}</view>题</view>
			</view>
		</view>
		<view class="content">
			<view class="modal" @click="toFamousList(item)" v-for="(item,index) in dataList" :key="index">
				<img v-show="index%5 ==0" src="../../static/index/teacherBg_one.png">
				<img v-show="index%5 ==1" src="../../static/index/teacherBg_two.png">
				<img v-show="index%5 ==2" src="../../static/index/teacherBg_three.png">
				<img v-show="index%5 ==3" src="../../static/index/teacherBg_four.png">
				<img v-show="index%5 ==4" src="../../static/index/teacherBg_five.png">
				<view v-if="type==0" class="icon" @click.stop="clickResets(item)">点击重置</view>
				<view class="progress">
					<view class="title">{{item.title}}</view>
					<view class="proInfo" v-if="type==0">
						<u-line-progress :percentage="item.two_leve_do_question_count/item.two_leve_question_count*100" :showText="false" height="6" activeColor="#ffd656"></u-line-progress>
						<view class="texts">{{item.two_leve_do_question_count}}/{{item.two_leve_question_count}}</view>
					</view>
					<view class="proInfo" v-else>——总共{{item.two_leve_question_count || item.two_leve_wrong_count || item.two_leve_collection_count}}题</view>
				</view>
			</view>
		</view>
		<!-- 加载动画 -->
		<view class="loadModul" v-if="showLoad">
			<u-loading-icon></u-loading-icon>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { famousBrushThreeMenu, famousBrushTwoMenu, famousBrushOneMenu, 
	famousBrushTwoMenuCollection, famousBrushOneMenuCollection, famousBrushTwoMenuWrong, famousBrushOneMenuWrong,
	famousBrushCollection, famousBrushWrong, resetFamous } from '@/common/http.api.js';
	export default {
		data() {
			return {
				onoff:true,
				showLoad:false,
				show:false, //显示确认窗
				models:'1', //白天模式
				current:0,
				resetId:'',
				type:0, //首页入口判断
				wrong:0,
				token:'',
				today_brush:'',
				total_brush:'',
				id:'',
				enter:'',
				currents:'',
				data:{},
				dataList:{}
			}
		},
		onLoad(option) {
			this.showLoad = true
			this.models = store.state.model
			this.token = store.state.vuex_token
			this.enter = option.enter
			this.currents = option.currents
			if(this.models=='0'){
				console.log('进入夜间模式')
				uni.setNavigationBarColor({
				    frontColor: '#ffffff',
				    backgroundColor: '#303030',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}else{
				console.log('进入白天模式')
				uni.setNavigationBarColor({
				    frontColor: '#000000',
				    backgroundColor: '#ffffff',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}
			if(option.enter==0){
				// 首页
				this.type = 0
				if(this.token){
					famousBrushThreeMenu({token:this.token}).then(res=>{
						console.log(res)
						this.data = res.data.data.menu
						this.today_brush = res.data.data.today_brush
						this.total_brush = res.data.data.total_brush
						for(let i in this.data){
							this.data[i].isChecked = false
						}
						this.data[0].isChecked = true
						this.id = res.data.data.menu[0].id
						famousBrushTwoMenu({token:this.token, id:this.id}).then(res=>{
							console.log(res)
							this.dataList = res.data.data
							this.showLoad = false
						}).catch(err=>{
							console.log(err)
						})
					}).catch(err=>{
						console.log(err)
					})
				}else{
					famousBrushThreeMenu().then(res=>{
						console.log(res)
						this.data = res.data.data.menu
						this.today_brush = res.data.data.today_brush
						this.total_brush = res.data.data.total_brush
						for(let i in this.data){
							this.data[i].isChecked = false
						}
						this.data[0].isChecked = true
						this.id = res.data.data.menu[0].id
						famousBrushTwoMenu({id:this.id}).then(res=>{
							console.log(res)
							this.dataList = res.data.data
							this.showLoad = false
						}).catch(err=>{
							console.log(err)
						})
					}).catch(err=>{
						console.log(err)
					})
				}
				
			}else{
				console.log("复习入口")
				// 复习
				this.type = 1
				if(option.currents==0){
					 //错题
					 this.wrong = 1
					 uni.setNavigationBarTitle({
						title: "名师名题错题集"
					 });
					 famousBrushWrong({token:this.token}).then(res=>{
					 	console.log(res)
					 	this.data = res.data.data
					 	for(let i in this.data){
					 		this.data[i].isChecked = false
					 	}
					 	this.data[0].isChecked = true
						this.id = res.data.data[0].id
						famousBrushTwoMenuWrong({token:this.token, id:this.id}).then(res=>{
							console.log(res)
							this.dataList = res.data.data
							this.showLoad = false
						}).catch(err=>{
							console.log(err)
						})
					 }).catch(err=>{
					 	console.log(err)
					 })
				}else{
					 //收藏
					 this.wrong = 2
					 uni.setNavigationBarTitle({
						title: "名师名题收藏集"
					 });
					 famousBrushCollection({token:this.token}).then(res=>{
					 	console.log(res)
					 	this.data = res.data.data
					 	for(let i in this.data){
					 		this.data[i].isChecked = false
					 	}
					 	this.data[0].isChecked = true
						this.id = res.data.data[0].id
						famousBrushTwoMenuCollection({token:this.token, id:this.id}).then(res=>{
							console.log(res)
							this.dataList = res.data.data
							this.showLoad = false
						}).catch(err=>{
							console.log(err)
						})
					 }).catch(err=>{
					 	console.log(err)
					 })
				}
			}
		},
		onShow() {
			if(this.currents==0){
				 //错题
				 famousBrushWrong({token:this.token}).then(res=>{
				 	console.log(res)
				 	this.data = res.data.data
				 	for(let i in this.data){
				 		this.data[i].isChecked = false
				 	}
				 	this.data[0].isChecked = true
					this.id = res.data.data[0].id
					famousBrushTwoMenuWrong({token:this.token, id:this.id}).then(res=>{
						console.log(res)
						this.dataList = res.data.data
						this.showLoad = false
					}).catch(err=>{
						console.log(err)
					})
				 }).catch(err=>{
				 	console.log(err)
				 })
			}
		},
		methods: {
			// 点击tab
			change(e){
				this.showLoad = true
				this.current = e
				console.log(e)
				console.log(typeof this.data)
				for(let i in this.data){
					this.data[i].isChecked = false
				}
				this.data[e].isChecked = true
				if(this.enter==0){
					// 首页
					famousBrushTwoMenu({token:this.token, id:this.data[e].id}).then(res=>{
						console.log(res)
						this.dataList = res.data.data
						this.showLoad = false
					}).catch(err=>{
						console.log(err)
					})
				}else{
					console.log("复习入口")
					// 复习
					if(this.currents==0){
						 //错题
						 famousBrushTwoMenuWrong({token:this.token, id:this.data[e].id}).then(res=>{
						 	console.log(res)
						 	this.dataList = res.data.data
							this.showLoad = false
						 }).catch(err=>{
						 	console.log(err)
						 })
					}else{
						 //收藏
						 famousBrushTwoMenuCollection({token:this.token, id:this.data[e].id}).then(res=>{
						 	console.log(res)
						 	this.dataList = res.data.data
							this.showLoad = false
						 }).catch(err=>{
						 	console.log(err)
						 })
					}
				}
				
			},
			// 去列表页
			toFamousList(e) {
				console.log(e)
				if(this.enter==0){
					// 首页
					famousBrushOneMenu({token:this.token, id:e.id}).then(res=>{
						console.log(res)
						uni.navigateTo({
							url: '/pages/index/famousList?info='+encodeURIComponent(JSON.stringify(res.data.data))+'&wrong='+this.wrong+'&title='+e.title+'&all='+e.two_leve_question_count+'&do='+e.two_leve_do_question_count
						})
					}).catch(err=>{
						console.log(err)
					})
				}else{
					console.log("复习入口")
					// 复习
					if(this.currents==0){
						 //错题
						 famousBrushOneMenuWrong({token:this.token, id:e.id}).then(res=>{
							console.log(res)
							uni.navigateTo({
								url: '/pages/index/famousList?info='+encodeURIComponent(JSON.stringify(res.data.data))+'&wrong='+this.wrong+'&title='+e.title+'&all='+e.two_leve_wrong_count
							})
						}).catch(err=>{
							console.log(err)
						})
					}else{
						//收藏
						famousBrushOneMenuCollection({token:this.token, id:e.id}).then(res=>{
							console.log(res)
							uni.navigateTo({
								url: '/pages/index/famousList?info='+encodeURIComponent(JSON.stringify(res.data.data))+'&wrong='+this.wrong+'&title='+e.title+'&all='+e.two_leve_collection_count
							})
						}).catch(err=>{
							console.log(err)
						})
					}
				}
			},
			// 点击重置弹出确认窗口
			clickResets(e) {
				this.resetId = e.id
				console.log(e)
				this.show = true
			},
			// 重置单元记录
			resets() {
				this.show = false
				resetFamous({token:this.token, unit_id:this.resetId}).then(res=>{
					console.log(res)
					this.$u.toast(res.data.data)
					famousBrush({token:this.token}).then(res=>{
						console.log(res)
						this.data = res.data.data.menu
						this.today_brush = res.data.data.today_brush
						this.total_brush = res.data.data.total_brush
						for(let i in this.data){
							this.data[i].isChecked = false
						}
						this.data[0].isChecked = true
					}).catch(err=>{
						console.log(err)
					})
				}).catch(err=>{
					console.log(err)
				})
				console.log("重置成功")
			}
		}
	}
</script>

<style l lang="scss">
	.loadModul{
		position: fixed;
		z-index: 999;
		background-color: rgba(192, 192, 192, .8);
		border-radius: 20rpx;
		width: 300rpx;
		height: 250rpx;
		left: 50%;
		right: 50%;
		top:50%;
		bottom: 50%;
		transform: translate(-50%,-50%);
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.tabar{
		display: flex;
		font-size: 28rpx;
		margin-bottom: 30rpx;
		.checked{
			background-color: #6ab4ff;
			color: #fff;
		}
		view{
			margin-right: 20rpx;
			padding: 8rpx 20rpx;
			width: 150rpx;
			white-space: nowrap;
			overflow: hidden;
			text-align: center;
			border-radius: 6rpx;
			text-overflow: ellipsis;
			background-color: #fff;
		}
	}
	.info{
		display: flex;
		margin: 40rpx 0;
		padding: 20rpx 0;
		justify-content: space-around;				
		background-color: #fff;
		border-radius: 6rpx;
		.boders{
			width: 1px;
			height: 90rpx;
			margin: 20rpx 0;
			border-left: 1px dashed #bebebe;
		}
		view{
			text-align: center;
			font-size: 32rpx;
			font-weight: 500;
			.text{
				font-size: 68rpx;
				font-weight: 600;
				margin-right: 6rpx;
				display: inline-block;
			}
		}
	}
	.content{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		.modal{
			width: 48%;
			border-radius: 10rpx;
			margin-bottom: 10rpx;
			position: relative;
			.icon{
				position: absolute;
				right: 0;
				top: 20rpx;
				background-color: #ffd656;
				color: #fff;
				font-size: 20rpx;
				text-align: center;
				width: 110rpx;
				line-height: 40rpx;
				border-top-left-radius: 50rpx;
				border-bottom-left-radius: 50rpx;
			}
			.progress{
				position: absolute;
				bottom: 20rpx;
				left: 30rpx;
				color: #fff;
				.proInfo{
					display: flex;
					margin-top: 10rpx;
					font-size: 28rpx;
					width: 260rpx;
					.texts{
						height: 12rpx;
						line-height: 12rpx;
						font-size: 22rpx;
						margin-left: 20rpx;
						padding-right: 30rpx;
					}
				}
			}
			img{
				width: 100%;
				height: 230rpx;
				border-radius: 10rpx;
			}
		}
	}
	.body{
		padding: 20rpx 40rpx;
		min-height: 100vh;
		background-color: #f7f7fb;
	}
	.bodyNight{
		padding: 20rpx 40rpx;
		min-height: 100vh;
		background-color: #3b3b3b;
		.tabar{
			.checked{
				background-color: #6ab4ff;
				color: #fff;
			}
			view{
				color: #fff;
				background-color: #303030;
			}
		}
		.info{			
			background-color: #303030;
			color: #fff;
		}
	}
</style>